var graceChecker = require("@/common/graceChecker.js");
export default {
	data() {
		return {
			tel: '',
			code: '',
			passWord: '',
			passWorda: '',
			second: 90,
			showText: true
		};
	},
	onLoad() {

	},
	methods: {
		sendCode() { //发送验证码
			var th = this;
			if (th.tel == "") {
				uni.showToast({
					icon: 'none',
					title: "请填写正确的手机号码"
				});
				return false;
			}

			//发送验证码
			th.$request.common
				.SendCode({
					tel: th.tel,
					typeId: 1
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {

						th.showText = false
						var interval = setInterval(() => {
							let times = --th.second
							th.second = times < 10 ? '0' + times : times //小于10秒补 0
						}, 1000)
						setTimeout(() => {
							clearInterval(interval)
							th.second = 90
							th.showText = true
						}, 90000)

					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});

		},
		formSubmit(e) {
			var _this = this;
			//定义表单规则
			var rule = [{
					name: "tel",
					checkType: "phoneno",
					errorMsg: "电话号码不正确"
				},
				{
					name: "code",
					checkType: "string",
					checkRule: "6,6",
					errorMsg: "验证码6个字符"
				},
				{
					name: "passWord",
					checkType: "string",
					checkRule: "6,20",
					errorMsg: "密码为6-20个字符"
				},
				{
					name: "passWorda",
					checkType: "same",
					checkRule: _this.passWord,
					errorMsg: "两次密码不相同"
				},
			];
			//进行表单检查
			var formData = e.detail.value;

			var checkRes = graceChecker.check(formData, rule)
			if (checkRes) {
				uni.showLoading({
					title: '数据提交中'
				});
				_this.$request.user
					.RetrievePwd(formData)
					.then(data => {
						uni.hideLoading();
						if (data.status == 1) {
							uni.navigateTo({
								url: '/pages/member/login'
							});
						} else {
							uni.showToast({
								icon: 'none',
								title: data.msg
							});
						}
					})
					.catch(err => {
						uni.hideLoading();
						//消息异常
						uni.showToast({
							icon: 'none',
							title: '数据加载异常'
						});
					});
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
			}
		}
	}
};
